  .hero {
    background-image: url("../img/hero/background-hero-dekstop@2x.webp");
    background-size: 100% 100%;
  }
}

.hero::after {
  position: absolute;
  content: "";
  width: inherit;
  height: 58px;
  background-size: cover;
  left: 0;
  bottom: 0;
  background-image: url("../img/hero-figure.svg");
  background-repeat: no-repeat;
  background-position: center;
}

