  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-transform: inherit;
}

.checkbox__label {
  cursor: pointer;
}

.checkbox__option::after {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  left: 0;
  top: -10px;
}

.checkbox__input:checked + .checkbox__option::before {
  position: absolute;
  content: "";
  left: 12px;
  top: 5px;
  z-index: 2;
  width: 22px;
  height: 16px;
  background-image: url("../img/tick.svg");
}

.checkbox__label:hover .checkbox__option::after {
  background-color: #e5e5e5;
}

.checkbox__label:active .checkbox__option::after {
  background-color: #d5d5d5;
}

.checkbox__label:active .checkbox__option::before {
  opacity: 30%;
}

