.radio__label {
  position: relative;
  cursor: pointer;
}

.radio__option {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-transform: inherit;
  padding: 10px 0;
}

.radio__option::after {
  content: "";
  position: absolute;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #f2f2f2;
  top: -10px;
  left: -61px;
}

.radio__label:hover .radio__option::after {
  background-color: #e5e5e5;
}

.radio__label:active .radio__option::after {
  background-color: #d5d5d5;
}

.radio__label:active .radio__option::before {
  opacity: 30%;
}

.radio__input:checked + .radio__option::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  top: 6px;
  left: -45px;
  z-index: 2;
  background-color: #333333;
  border-radius: 50%;
}

.radio__input:hover {
  background-color: #e5e5e5;
}
.radio__input:active {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
  outline-offset: -4px;
}

