}

.sedona-map__image::before {
  content: "";
  position: absolute;
  width: 27px;
  height: 27px;
  border-radius: 50%;
  background-color: #ffffff;
  left: 50%;
  top: 50%;
  transform: translate(-50%);
}

.sedona-map__image::after {
  content: "";
  position: absolute;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: #81b3d3;
  left: 50%;
  top: 51.5%;
