.main-header {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  min-height: 56px;
}

.main-header__logo {
  display: block;
  width: 185px;
  height: 95px;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}
